import React from "react";
import All from "../img/Layer 27.png";
import Face from "../img/Layer 21.png";



function Cont5(props) {
	return(
		<>
			<div className="cont5">
				<div className="cont5all">
					<span className="cont5span">Photography</span>
					<h2 className="cont5h2">Lorem ipsum dolor sit amet <br /> consectetur adipisicing elit</h2>
					<div className="cont5author">
						<img className="faceimg" src={Face} />
						<p className='author'>Blaz Robar, god like web designer</p>
					</div>
				</div>
			</div>
		</>
	);
}



export default Cont5;