import React from "react";
import Back from "../img/Layer 25.png";


function Nav(props) {
	return(
		<>
			<div className="nav">
				<div className="nav_parth">
					<img src={props.src1} />
					<span className="nav_span">{props.navtext}</span>
				</div>

				<div className="nav_loc">
					<a className="nav_loc_item" href={props.href}>About</a>
					<a className="nav_loc_item" href={props.href}>How It Works</a>
					<a className="nav_loc_item" href={props.href}>Services</a>
					<a className="nav_loc_item" href={props.href}>FAQ</a>
					<a className="nav_loc_item" href={props.href}>Contact</a>
					<a className="more"> MENU  
						<ul className="nav_loc_sm">
							<li className="nav_loc_items"><a className="nav_loc_item2" href={props.href}>About</a></li>
							<li className="nav_loc_items"><a className="nav_loc_item2" href={props.href}>How It Works</a></li>
							<li className="nav_loc_items"><a className="nav_loc_item2" href={props.href}>Services</a></li>
							<li className="nav_loc_items"><a className="nav_loc_item2" href={props.href}>FAQ</a></li>
							<li className="nav_loc_items"><a className="nav_loc_item2" href={props.href}>Contact</a></li>
							<li className="nav_sm_form">
								<div className="form smform">
									<input className="smforminput" placeholder="Search Website"/>
									<span className="smsearch">Q</span>
								</div>
							</li>
						</ul>
					</a>

					<div className="form">
						<input className="forminput" placeholder="Search Website"/>
						<button className="search">Q</button>
					</div>
				</div>
			</div>
			<div className="back">
				<img className="logoimg" src={Back} />
			</div>

		</>
	);
}



export default Nav; 