import React from "react";


function Logo(props) {
	return(
		<>
			<div className="logo">
				<h1 className="logotitle">Designs your eyeballs <br /> will thank you for</h1>
				<button className="logobtn">Get Started Today</button>
				<p className="logotext">Lorem ipsum dolor sit amet, consectetur</p>
			</div>
		</>
	);
}



export default Logo;